<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_customer">
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
        <FormAE ref="FormAE" :apis="apis" @refresh="$refs.list.getList()" FormsCode="oa_customer" :loadAfterFun="loadAfterFun" />
        <FormDetails ref="FormDetails" :apis="apis" FormsCode="oa_customer" />
    </div>
</template>

<script>
export default {
    name: "CustomerList",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
        FormAE: () => import("@/universalPage/formAE/FormAE.vue"),
        FormDetails: () => import("@/universalPage/formDetails/FormDetails.vue")
    },
    data() {
        return {
            apis: {
                edit: this._api.CRM_CustomerEdit,
                create: this._api.CRM_CustomerCreate,
                list: this._api.CRM_CustomerManage,
                delete: this._api.CRM_CustomerDelete,
                audit: this._api.CRM_CustomerAudit,
                details: this._api.CRM_CustomerDetails
            },
            fieldToPage: {
                FNumber: this.toDetails
            }
        }
    },
    methods: {
        render(h) {
            return (
                <div>
                    <el-table-column prop="FNumber" label="许可证号" width="120" align="left" header-align="center"></el-table-column>
                    <el-table-column prop="FBusinessLicenseNumber" label="社会统一信用代码" width="160" align="center"></el-table-column>
                    <el-table-column prop="FCustName" label="商户名称" width="" align="center"></el-table-column>
                    <el-table-column prop="FChairman" label="法人" width="120" align="center"></el-table-column>
                    <el-table-column prop="FMobile" label="联系方式" width="120" align="center"></el-table-column>
                    <el-table-column prop="ClassName" label="类型" width="" align="center"></el-table-column>
                    <el-table-column prop="FAddress" label="地址" width="" align="center"></el-table-column>
                    <el-table-column prop="CreateName" label="创建人" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="创建时间" width="160" align="center"></el-table-column>
                </div >
            )
        },
        toDetails(row) {
            this.$refs.FormDetails.start(row)
        },
        loadAfterFun(info, that) {
            that.$refs.class.back(info.ClassId)
        }
    },
    mounted() { }
}
</script>

